<template>
  <div class="bank">
    <small-title txt="" color="#59C1D2"></small-title>
    <form>
      <inp-line placeTxt="" v-model="formData.name"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt=""
                v-model="formData.bankAccount"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt=""
                :baseInp="false" :searchList="bankList"
                @seleBank="setBankSele"
                v-model="formData.bankName"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt="18"
                v-model="formData.idCode"
                v-validate="'required|min: 18|max: 18'"
                name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <!--<inp-line placeTxt="6" inpType="password"-->
      <!--v-model="formData.bankPwd"-->
      <!--v-validate="'required'" name="">-->
      <!--<span slot="left" class="left"></span>-->
      <!--</inp-line>-->
      <inp-line placeTxt="" inpType="password"
                v-model="formData.pwd"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt="" inpType="password"
                v-model="formData.pwd2"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt="" v-model="formData.phone"
                v-validate="'required|mobile'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt="" v-model="formData.imgCode"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
        <img slot="right" class="right imgCode" :src="imgCodeSrc" alt="...">
      </inp-line>
      <inp-line placeTxt="" v-model="formData.msgCode"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
        <div slot="right" class="right" @click="getMsgCode"></div>
      </inp-line>
      <div class="submit" @click="getBind"></div>
      <div class="back" @click="$router.go(-1)">&lt;</div>
    </form>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';
  import smallTitle from '../../components/common/smallTitle.vue';
  import inpLine from '../../components/common/inpLine.vue';

  export default {
    components: {smallTitle, inpLine},
    mounted () {
      this.initBankList();  // inp
    },
    data () {
      return {
        formData: {
          name: null,
          bankAccount: null,
          bankName: null,
          idCode: null, // 
          bankPwd: 0,  // 0
          pwd: null,  // 
          pwd2: null, // 
          phone: null,  // 
          imgCode: null,
          msgCode: null
        },
        bankList: [],  // 
        bankSele: {}  // 
      };
    },
    computed: {
      imgCodeSrc () {
        if (this.formData.phone && !this.errors.has('')) {
          return 'http://api.haozhaoli510.com/authCode?mobile=' + this.formData.phone;
        } else {
          return;
        }
      }
    },
    methods: {
      initBankList () {
        Ajax.get({api: 'bank_bind'}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            data.bank_list.forEach(item => {
              item.value = item.fields.name;
            });
            this.bankList = data.bank_list;
          }
        });
      },
      setBankSele (item) {
        // inpbankSele
        this.bankSele = item;
      },
      getMsgCode () {
        // 
        let data = {
          mobile: this.formData.phone,
          imageCode: this.formData.imgCode,
          type: '4' // 1  2  3  4 
        };
        Ajax.save({api: 'send_msg'}, data).then(res => {
          if (res.body.status === 200) {
            this.$notify({title: '', type: 'success'});
          }
        });
      },
      getBind () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            if (this.formData.pwd === this.formData.pwd2) {
              this.ajaxGetBind();  // ajax
            } else {
              this.$alert('', '');
            }
          } else {
            this.$alert(this.errors.all()[0], '');  // 
          }
        });
      },
      ajaxGetBind () {
        let data = {
          bank_id: this.bankSele.pk,
          real_name: this.formData.name,
          bank_account: this.formData.bankAccount,
          bank_name: this.formData.bankName,
          id_card: this.formData.idCode,
          bank_password: this.formData,
          pay_password: this.formData.pwd,
          bank_mobile: this.formData.phone,
          check_code: this.formData.msgCode
        };
        Ajax.save({api: 'bank_bind'}, data).then(res => {
          if (res.body.status === 200) {
            this.$notify({message: '', type: 'success'});
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .bank {
    width: 585px;
    margin: 40px auto 0;
  }

  .smallTitle {
    margin-bottom: 40px;
  }

  form {
    width: 100%;
    .inp-line {
      .left {
        box-sizing: border-box;
        width: 155px;
        padding: 0 20px;
        text-align-last: justify;
      }
      .right {
        padding: 0 10px;
        border-left: 1px solid #959595;
        cursor: pointer;
      }
      .imgCode {
        width: 5em;
        padding: 0;
        text-align: center;
      }
    }
    .submit {
      width: 100%;
      height: 64px;
      text-align: center;
      background-color: @cCyan;
      cursor: pointer;
      line-height: 64px;
      font-size: 24px;
      color: @cWhite;
    }
    .back {
      margin: 10px 0 100px;
      text-align: center;
      font-size: 24px;
      color: @cCyan;
    }
  }
</style>



