<template>
  <div class="perfect" style="line-height: 150%">
    <div class="content">
      <div class="message">
        {{info.grade}}<br/>
        {{info.account}}<br/>
        {{info.regTime}}<br/>
        {{info.realName || ''}}<br/>
        {{info.mobile}}<br/>
        {{info.ide || ''}}<br/>
      </div>
      <div class="occupation">
        <inp-line placeTxt=""
                  v-model="formData.occ"
                  v-validate.initial="'required'" name=""
                  :class="{danger: errors.has('')}">
          <div slot="left" class="inp-left"></div>
        </inp-line>
        <inp-line placeTxt=""
                  v-model="formData.hobby"
                  v-validate.initial="'required'" name=""
                  :class="{danger: errors.has('')}">
          <div slot="left" class="inp-left"></div>
        </inp-line>
        <inp-line placeTxt=""
                  v-model="formData.qq"
                  v-validate.initial="'required'" name="QQ"
                  :class="{danger: errors.has('QQ')}">
          <div slot="left" class="inp-left">QQ</div>
        </inp-line>
        <inp-line placeTxt=""
                  v-model="formData.wx"
                  v-validate.initial="'required'" name=""
                  :class="{danger: errors.has('')}">
          <div slot="left" class="inp-left"></div>
        </inp-line>
        <inp-line placeTxt=""
                  v-model="formData.email"
                  v-validate.initial="'required|email'" name=""
                  :class="{danger: errors.has('')}">
          <div slot="left" class="inp-left"></div>
        </inp-line>
      </div>
      <div class="emergency">
        <inp-line placeTxt=""
                  v-model="formData.eme.urgent"
                  v-validate.initial="'required'" name=""
                  :class="{danger: errors.has('')}">
          <div slot="left" class="inp-left"></div>
        </inp-line>
        <inp-line placeTxt=""
                  v-model="formData.eme.rel"
                  v-validate.initial="'required'" name=""
                  :class="{danger: errors.has('')}">
          <div slot="left" class="inp-left"></div>
        </inp-line>
        <inp-line placeTxt=""
                  v-model="formData.eme.mobile"
                  v-validate.initial="'required|mobile'" name=""
                  :class="{danger: errors.has('')}">
          <div slot="left" class="inp-left"></div>
        </inp-line>
      </div>
      <div class="optionBox">
        <div class="btns cancel" @click="$router.go(-1)"> </div>
        <div class="btns submit" @click="submitInfo"> </div>
      </div>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';
  import inpLine from '../../../components/common/inpLine.vue';

  export default {
    components: {inpLine},
    mounted () {
      this.initInfo();  // 
    },
    data () {
      return {
        info: {
          grade: null,
          account: null,
          regTime: null,
          realName: null,
          mobile: null,
          ide: null
        },
        formData: {
          occ: null,  // 
          hobby: null,  // 
          qq: null, // QQ
          wx: null, // 
          email: null, // 
          eme: {
            urgent: null, // 
            rel: null, // 
            mobile: null // 
          }
        }
      };
    },
    methods: {
      initInfo () {
        Ajax.get({api: 'show_base_info'}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.info.grade = data.info_msg.level;
            this.info.account = data.info_msg.account;
            this.info.regTime = data.info_msg.create_time;
            this.info.realName = data.info_msg.real_name;
            this.info.mobile = data.info_msg.mobile;
            this.info.ide = data.info_msg.id_card;
          }
        });
      },
      submitInfo () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            this.ajaxSubmitInfo();  // ajax
          } else {
            this.$alert(this.errors.all()[0], '');  // 
          }
        });
      },
      ajaxSubmitInfo () {
        let data = {
          job: this.formData.occ,
          hobby: this.formData.hobby,
          qq: this.formData.qq,
          wechat: this.formData.wx,
          email: this.formData.email,
          emergency_contact_name: this.formData.eme.urgent,
          emergency_contact_relation: this.formData.eme.rel,
          emergency_contact_mobile: this.formData.eme.mobile
        };
        Ajax.save({api: 'complete_user_info'}, data).then(res => {
          if (res.body.status === 200) {
            this.$notify.success({title: ''});
            this.$router.push('/personal/info/message');
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../../assets/css/common.less";

  .content {
    font-size: 23px;
    .message, .occupation, .emergency {
      color: #606060;
      line-height: 200%;
    }
    .occupation, .emergency {
      margin-top: 50px;
    }
    .emergency {
      display: flex;
    }
    .inp-left {
      padding-left: 20px;
      padding-right: 20px;
    }
    .optionBox {
      width: 100%;
      margin-top: 80px;
      margin-bottom: 100px;
      display: flex;
      .btns {
        padding: 23px 100px;
        margin: 10px;
        cursor: pointer;
        background-color: @cGray;
        color: white;
      }
      .submit {
        background-color: @cCyan;
      }
    }
    .danger {
      border: 1px solid red;
    }
  }

</style>



