<template>
  <div class="registerForm">
    <form>
      <inp-line placeTxt="" v-model="formData.username"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt="" inpType="password"
                v-model="formData.pwd" v-validate="'required'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt="" inpType="password"
                v-model="formData.pwd2" v-validate="'required'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt="" v-model="formData.phone"
                v-validate="'required|mobile'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt="" v-model="formData.imgCode"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
        <img slot="right" class="right imgCode" :src="imgCodeSrc" alt="...">
      </inp-line>
      <inp-line placeTxt="" v-model="formData.msgCode"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
        <div slot="right" class="right" @click="getMsgCode"></div>
      </inp-line>
      <div class="submit" @click="submit"></div>
    </form>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '../../api';
  import inpLine from '../common/inpLine.vue';

  export default {
    components: {inpLine},
    data () {
      return {
        formData: {
          username: null,
          pwd: null,
          pwd2: null,
          phone: null,
          imgCode: null,  // 
          msgCode: null
        }
      };
    },
    computed: {
      imgCodeSrc () {
        if (this.formData.phone && !this.errors.has('')) {
          return 'http://api.haozhaoli510.com/authCode?mobile=' + this.formData.phone;
        } else {
          return;
        }
      }
    },
    methods: {
      getMsgCode () {
        // 
        let data = {
          mobile: this.formData.phone,
          imageCode: this.formData.imgCode,
          type: '1' // 1  2  3 
        };
        Ajax.save({api: 'send_msg'}, data).then(res => {
          if (res.body.status === 200) {
          } else {
            this.$alert(res.body.msg);
          }
        }, errs => {
        });
      },
      submit () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            if (this.formData.pwd === this.formData.pwd2) {
              this.ajaxRegister();  // ajax
            } else {
              this.$alert('', '');
            }
          } else {
            this.$alert(this.errors.all()[0], '');  // 
          }
        });
      },
      ajaxRegister () {
        let data = {
          check_code: this.formData.msgCode,
          mobile: this.formData.phone,
          pwd: this.formData.pwd,
          username: this.formData.username
        };
        Ajax.save({api: 'register'}, data).then(res => {
          if (res.body.status === 200) {
            this.$message({message: '', type: 'success'});
            this.$router.push('/home');
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .registerForm {

  }

  form {
    width: 100%;
    .inp-line {
      .left {
        box-sizing: border-box;
        width: 155px;
        padding: 0 20px;
        text-align-last: justify;
      }
      .right {
        padding: 0 10px;
        border-left: 1px solid #959595;
        cursor: pointer;
      }
      .imgCode {
        width: 5em;
        padding: 0;
        text-align: center;
      }
    }
    .submit {
      width: 100%;
      height: 64px;
      margin-bottom: 100px;
      text-align: center;
      background-color: @cCyan;
      cursor: pointer;
      line-height: 64px;
      font-size: 24px;
      color: @cWhite;
    }
  }
</style>



