<template>
  <div class="essential">
    <div class="content">
      <div class="message">
        {{info.level}}<br/>
        {{info.account}}<br/>
        {{info.regTime}}<br/>
        {{info.realName || ''}}<br/>
        {{info.mobile}}<br/>
        {{info.ide || ''}}<br/>
      </div>
      <div class="integral">
        <div class="available">{{info.available}}</div>
        <div class="convertible">
          {{info.convertible}}
          <div class="cWithdrawals" @click="$router.push('/mission/get')">
            
          </div>
        </div>
      </div>
      <div class="recharge">
        {{info.rechargeAmount}}
        <div class="rWithdrawals">
          
        </div>
      </div>
      <div class="bank-box">
        <div class="bank">{{info.bankAccount || ''}}</div>
        <div class="btn-box">
          <span class="btns binding"
                @click="$router.push('/personal/bank')"></span>
          <span class="bar">|</span>
          <span class="btns unbinding"></span>
        </div>
      </div>
      <div class="btns-line">
        <div class="btns recharge"> </div>
        <div class="btns transferAccounts"> </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';

  export default {
    mounted () {
      this.initInfo();  // 
    },
    data () {
      return {
        info: {
          level: '', // 
          account: 'HZL0123',
          regTime: '2017-10-20', // 
          realName: '',
          mobile: '13565487455',
          ide: '123456789123456789',  // 
          available: 0, // 
          convertible: 0, // 
          rechargeAmount: 0,  // 
          bankAccount: null // 
        }
      };
    },
    methods: {
      initInfo () {
        Ajax.get({api: 'show_base_info'}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.info.level = data.info_msg.level;
            this.info.account = data.info_msg.account;
            this.info.regTime = data.info_msg.create_time;
            this.info.realName = data.info_msg.real_name;
            this.info.mobile = data.info_msg.mobile;
            this.info.ide = data.info_msg.id_card;
            this.info.available = data.info_msg.avaliable_intergral;
            this.info.convertible = data.info_msg.intergral_num;
            this.info.rechargeAmount = data.info_msg.gold_num;
            this.info.bankAccount = data.info_msg.bank_account;
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../../assets/css/common.less";

  .essential {
    line-height: 150%;
  }

  .content {
    font-size: 23px;
    color: #606060;
    .message {
      line-height: 200%;
      .account {
        font-size: 19px;
      }
    }
    .integral {
      display: flex;
      margin-top: 5px;
      margin-bottom: 15px;
      align-items: center;
      .available {
        margin-right: 50px;
      }
      .convertible {
        display: flex;
        align-items: center;
        .cWithdrawals {
          margin-left: 20px;
          background-color: @cCyan;
          padding: 10px 20px;
          color: @cWhite;
          cursor: pointer;
        }
      }
    }
    .bank-box {
      display: flex;
      margin-top: 20px;
      .bank {
        margin-right: 50px;
      }
      .btn-box {
        .bar {
          margin: 0 5px;
        }
        .btns {
          padding-left: 10px;
          padding-right: 10px;
          cursor: pointer;
        }
        .binding {
          color: @cCyan;
        }
        .unbinding {
          color: @cOrange;
        }
      }
    }
    .recharge {
      display: flex;
      align-items: center;
      .rWithdrawals {
        margin-left: 20px;
        background-color: @cCyan;
        padding: 10px 20px;
        color: @cWhite;
        cursor: pointer;
      }
    }
  }

  .btns-line {
    width: 100%;
    margin: 80px auto 100px;
    display: flex;
    .btns {
      padding: 23px 100px;
      margin: 10px;
      background-color: @cCyan;
      color: white;
    }
  }
</style>



