import {Message, Notification} from 'element-ui';
import Vue from 'vue';
import VueResource from 'vue-resource';

Vue.use(VueResource);

// 
Vue.http.interceptors.push(function (req, next) {
  if (process.env.NODE_ENV !== 'production') {
    console.log('req:', req);
  }
  // res
  next(function (res) {
    if (process.env.NODE_ENV === 'production') {
      if (res.body.status === 500) {
        Message.error(res.body.msg);
      }
    } else {
      console.log('res:', res);
      if (res.body.status === 200) {
        Message('' + res.url);
      } else if (res.body.status === 500) {
        Notification.error({
          title: '!',
          message: ':' + res.url + 'msg:' + res.body.msg
        });
      } else if (res.status === 500) {
        Notification.error({
          title: '!',
          message: 'Network'
        });
      }
    }
  });
});

Vue.http.options.credentials = true;  // 
Vue.http.options.emulateJSON = true;  // form

const API_ROOT = process.env.NODE_ENV === 'production' ? 'http://localhost:8080/api/' : 'http://192.168.1.104:8080/api/';
export default Vue.resource(API_ROOT + '{api}' + '/');

