<template>
  <div class="uploadForm">
    <div class="left">
      <img class="leftImg" :class="{danger: errors.has('')}"
           :src="formData.img">
      <div class="btn-line">
        <div class="loadBtn">
          
          <input type="file" class="imgInp" ref="imgInp" @change="onPhoto"
                 accept="image/jpg,image/jpeg,image/png"
                 v-validate.initial="'required'" name="">
        </div>
        <i class="bar">|</i>
        <div class="delBtn" @click="$emit('subtractForm',id)"></div>
      </div>
    </div>
    <div class="right">
      <form>
        <inp-line placeTxt="" v-model="formData.name"
                  v-validate.initial="'required'" name=""
                  :class="{danger: errors.has('')}">
          <div slot="left" class="inp-left"></div>
        </inp-line>
        <inp-line placeTxt="" v-model="formData.detailAddress"
                  v-validate.initial="'required'" name=""
                  :class="{danger: errors.has('')|| formData.seleAddress === null}">
          <div slot="left" class="inp-left-line">
            <span class="inp-left"></span>
            <el-cascader :options="addressList"
                         @active-item-change="getMoreAddress"
                         :props="cascaderInit"
                         v-model="formData.seleAddress">
            </el-cascader>
          </div>
        </inp-line>
        <inp-line placeTxt=""
                  v-model="formData.phone"
                  v-validate.initial="'required'" name=""
                  :class="{danger: errors.has('')}">
          <div slot="left" class="inp-left"></div>
        </inp-line>
        <inp-line placeTxt=""
                  v-model="formData.trade"
                  v-validate.initial="'required'" name=""
                  :class="{danger: errors.has('')}">
          <div slot="left" class="inp-left"></div>
        </inp-line>
        <inp-line placeTxt=""
                  v-model="formData.feature"
                  v-validate.initial="'required'" name=""
                  :class="{danger: errors.has('')}">
          <div slot="left" class="inp-left"></div>
        </inp-line>
      </form>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';
  import inpLine from '../../../components/common/inpLine.vue';

  export default {
    components: {inpLine},
    props: {
      id: {
        type: Number  // formid
      }
    },
    mounted () {
      this.initProList();
    },
    data () {
      return {
        formData: {
          name: null, // 
          seleAddress: null,  // 
          detailAddress: null,  // 
          trade: null,  // 
          feature: null, // 
          img: null// 
        },
        addressList: [  // 
          {
            label: '',
            children: [
              {
                label: '',
                children: [
                  {label: ''},
                  {label: ''}
                ]
              }
            ]
          }
        ],
        cascaderInit: { // 
          value: 'label'
        }
      };
    },
    watch: {
      formData: {
        handler (newVal) {
          this.$emit('updateForm', newVal, !this.errors.any(), this.id);
        },
        deep: true
      }
    },
    methods: {
      initProList () {
        this.ajaxProList();
      },
      onPhoto (index) {
        // 
        let inp = this.$refs.imgInp;
        if (inp.files && inp.files[0]) {
          // console.log(inp.files[0].size);
          this.condense(inp.files[0], index);
        }
      },
      condense (file, index) {
        // 
        let that = this;
        var reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function (e) {
          var img = new Image();
          var width = 355;  // image resize
          var quality = 1;  // image quality
          var canvas = document.createElement('canvas');
          var drawer = canvas.getContext('2d');
          img.src = this.result;
          img.onload = function () {
            canvas.width = width;
            canvas.height = width * (img.height / img.width);
            drawer.drawImage(img, 0, 0, canvas.width, canvas.height);
            let resImg = canvas.toDataURL('image/jpeg', quality);  // 
            that.formData.img = resImg;
          };
        };
      },
      getMoreAddress (val) {
        // val === []
        if (val.length === 1) {
          // 
          for (let key of Object.keys(this.addressList)) {
            if (val[0] === this.addressList[key].label) {
              this.ajaxCityList(
                this.addressList[key].label,
                (val) => {
                  this.addressList[key].children = val;
                });
              return;
            }
          }
        } else if (val.length === 2) {
          // 
          for (let key of Object.keys(this.addressList)) {
            if (val[0] === this.addressList[key].label) {
              for (let key2 of Object.keys(this.addressList[key].children)) {
                if (val[1] === this.addressList[key].children[key2].label) {
                  this.ajaxAreaList(
                    this.addressList[key].children[key2].label,
                    (val) => {
                      this.addressList[key].children[key2].children = val;
                    });
                  return;
                }
              }
              return;
            }
          }
        }
      },
      ajaxProList () {
        // 
        Ajax.get({api: 'get_task_province'}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            let fixed = [];
            data.province_list.forEach((item, index) => {
              if (!fixed[index]) fixed.push({children: []});
              fixed[index].label = item;
            });
            this.addressList = fixed;
          }
        });
      },
      ajaxCityList (data, reback) {
        // 
        Ajax.save({api: 'get_task_city'}, {province: data}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            let fixed = [];
            data.cities_list.forEach((item, index) => {
              if (!fixed[index]) fixed.push({children: []});
              fixed[index].label = item.fields.num;
            });
            console.log(fixed);
            reback(fixed);
          }
        });
      },
      ajaxAreaList (data, reback) {
        // 
        Ajax.save({api: 'get_task_area'}, {city: data}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            let fixed = [];
            data.areas_list.forEach((item, index) => {
              if (!fixed[index]) fixed.push({});
              fixed[index].label = item.fields.areaname;
            });
            console.log(fixed);
            reback(fixed);
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../../assets/css/common.less";

  .uploadForm {
    width: 100%;
    margin: 50px 0;
    display: flex;
    justify-content: space-between;
  }

  .left {
    width: 355px;
    img {
      display: inline-block;
      box-sizing: border-box;
      width: 355px;
      height: 355px;
      border: 1px solid @cBorder;
    }
    .danger {
      border: 1px solid red; // 
    }
    .btn-line {
      margin-top: 18px;
      display: flex;
      justify-content: center;
      font-size: 24px;
      color: @cCyan;
      .loadBtn, .delBtn {
        position: relative;
        display: inline-block;
        width: 4em;
        cursor: pointer;
        .imgInp {
          position: absolute;
          top: 0;
          left: 0;
          width: 4em;
          opacity: 0;
        }
      }
      .bar {
        display: inline-block;
        margin: 0 10px;
      }
    }
  }

  .right {
    width: 710px;
    form {
      .inp-left {
        display: inline-block;
        width: 4em;
        text-align: center;
        &::after {
          content: '|';
          margin: 0 5px 0 15px;
        }
      }
      input {
        padding: 0;
      }
      .danger {
        border: 1px solid red; // 
      }
    }
  }
</style>



