<template>
  <div class="personalMessage" style="line-height: 150%">
    <div class="content">
      <div class="message">
        {{info.level}}<br/>
        {{info.account}}<br/>
        {{info.regTime}}<br/>
        {{info.realName || ''}}<br/>
        {{info.mobile}}<br/>
        {{info.ide || ''}}<br/>
        {{info.homeAddress || ''}}<br/>
        {{info.address || ''}}<br/>
      </div>
      <div class="divider"></div>
      <div class="occupation">
        {{info.occ || ''}}<br/>
        {{info.hobby || ''}}<br/>
        Q Q{{info.qq || ''}}<br/>
        {{info.wx || ''}}<br/>
        {{info.email || ''}}<br/>
      </div>
      <div class="divider"></div>
      <div class="emergency-line">
        <div class="urgent">{{info.eme.urgent || ''}}</div>
        <div class="rel">{{info.eme.rel || ''}}</div>
        <div class="mobile">{{info.eme.mobile || ''}}</div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';

  export default {
    mounted () {
      this.initInfo();  // 
    },
    data () {
      return {
        info: {
          level: null,
          account: null,
          regTime: null,
          realName: null,
          mobile: null,
          ide: null,
          homeAddress: null,
          address: null,
          occ: null,
          hobby: null,
          qq: null,
          wx: null,
          email: null,
          eme: {
            urgent: '',
            rel: null,
            mobile: null
          }
        }
      };
    },
    methods: {
      initInfo () {
        Ajax.get({api: 'show_personal_info'}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.info.level = data.info_msg.level;
            this.info.account = data.info_msg.account;
            this.info.regTime = data.info_msg.create_time;
            this.info.mobile = data.info_msg.mobile;
            this.info.ide = data.info_msg.id_card;
            this.info.occ = data.info_msg.job;
            this.info.hobby = data.info_msg.hobby;
            this.info.qq = data.info_msg.qq;
            this.info.wx = data.info_msg.wechat;
            this.info.email = data.info_msg.email;
            this.info.eme.urgent = data.info_msg.emergency_contact_name;
            this.info.eme.rel = data.info_msg.emergency_contact_relation;
            this.info.eme.mobile = data.info_msg.emergency_contact_mobile;
          }
        });
      }
    }
  }
  ;
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../../assets/css/common.less";

  .content {
    font-size: 23px;
    margin-bottom: 100px;
    .message, .occupation, .emergency-line {
      color: @cLight;
      line-height: 200%;
    }
    .divider {
      width: 100%;
      height: 1px;
      background-color: #2ca3b6;
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }

  .emergency-line {
    display: flex;
    margin-bottom: 10px;
    justify-content: space-between;
    .binding, .unbundling {
      padding-left: 10px;
      padding-right: 10px;
      cursor: pointer;
    }
    .btns-box {
      .btns {
        cursor: pointer;
      }
      .change {
        color: @cCyan;
      }
      .del {
        color: @cOrange;
      }
      .bar {
        margin: 0 5px;
      }
    }
    .urgent, .rel, .mobile {
      margin-right: 50px;
    }
  }
</style>



