<template>
  <div class="loginForm">
    <form>
      <input type="text" class="username" placeholder=""
             v-model="formData.username"
             v-validate="'required'" name="">
      <input type="password" class="pwd" placeholder=""
             v-model="formData.pwd"
             v-validate="'required'" name="">
    </form>
    <div class="check-box">
      <label><input type="checkbox"></label>
      <label><input type="checkbox"></label>
    </div>
    <div class="forgetPwd-line">
      <router-link to="/forgetPwd" tag="span" class="forgetPwd">
        
      </router-link>
    </div>
    <div class="routers-box">
      <div class="btn login" @click="login"></div>
      <router-link to="/register" tag="div" class="btn register">
        
      </router-link>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '../../api';
  import Bus from '../../utils/bus.js';

  export default {
    data () {
      return {
        formData: {
          username: null,
          pwd: null
        }
      };
    },
    methods: {
      login () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            this.ajaxLogin();  // ajax
          } else {
            this.$alert(this.errors.all()[0], '');  // 
          }
        });
      },
      ajaxLogin () {
        let data = {
          username: this.formData.username,
          pwd: this.formData.pwd
        };
        Ajax.save({api: 'login'}, data).then(res => {
          if (res.body.status === 200) {
            this.$message({message: '', type: 'success'});
            this.ajaxGetInfo();
            this.$router.push('/home');
          }
        });
      },
      ajaxGetInfo () {
        Ajax.get({api: 'show_base_info'}).then(res => {
          let data = res.body.info_msg;
          localStorage.setItem('account', data.account);  // 
          localStorage.setItem('mobile', data.mobile);  // 
          localStorage.setItem('real_name', data.real_name);  // 
          localStorage.setItem('id_card', data.id_card);  // id
          localStorage.setItem('create_time', data.create_time);  // 
          localStorage.setItem('intergral_num', data.intergral_num);  // 
          localStorage.setItem('gold_num', data.gold_num);  // 
          Bus.$emit('logined');
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import '../../assets/css/common.less';

  .loginForm {
    width: 100%;
  }

  form {
    .username, .pwd {
      display: block;
      width: 100%;
      height: 64px;
      padding: 0 1em;
      border: 1px solid @cWhite;
      margin-bottom: 20px;
      background-color: @cCyan;
      font-size: 24px;
      color: @cWhite;
    }
  }

  .check-box {
    label {
      margin-right: 10px;
      font-size: 18px;
      color: @cWhite;
    }
    input[type="checkbox"] {
      vertical-align: middle;
    }
  }

  .forgetPwd-line {
    span {
      float: right;
      cursor: pointer;
      font-size: 18px;
      color: @cWhite;
    }
  }

  .routers-box {
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
    .btn {
      width: 185px;
      height: 54px;
      cursor: pointer;
      text-align: center;
      line-height: 54px;
      font-size: 24px;
      font-weight: bold;
    }
    .login {
      background-color: @cBorder;
      color: @cDarkCyan;
    }
    .register {
      background-color: @cDarkCyan;
      color: @cBorder;
    }
  }
</style>



