<template>
  <div class="tableComplete">
    <div class="content">
      <div class="table-box">
        <div class="title-line">
          <h3>/<label>*</label></h3>
          <span>&gt;&gt;</span>
        </div>
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column prop="province" label="" width="100">
            <template scope="scope">
              <el-dropdown v-if="scope.$index === 0" @command="seleProvince">
                <span class="el-dropdown-link">
                  {{provinceSele}}
                  <i class="el-icon-caret-bottom el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item v-for="(item, index) in provinceList"
                                    :command="item" :key="index">
                    {{item}}
                  </el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </template>
          </el-table-column>
          <el-table-column prop="city" label="" width="80">
          </el-table-column>
          <el-table-column prop="area" label="">
          </el-table-column>
          <el-table-column prop="address" label="">
          </el-table-column>
          <el-table-column prop="total" label="" width="120">
          </el-table-column>
          <el-table-column prop="claimed" label="" width="120">
          </el-table-column>
          <el-table-column prop="completed" label="" width="120">
          </el-table-column>
          <el-table-column prop="get" label="" width="120">
            <template scope="scope">
              <el-button v-if="scope.row.city" type="text"
                         @click="getCash(scope)"
                         :disabled="scope.row.claimed <= 100 ||
                         scope.row.claimed !== scope.row.completed">
                
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';

  export default {
    mounted () {
      this.ajaxGetProList();  // 
      this.ajaxPersonTasks(); // 
    },
    data () {
      return {
        pickerOptions: {
          disabledDate (time) {
            return time.getTime() > Date.now(); // 
          }
        },
        dateSele: Date.now(),  // 
        provinceSele: '',  // 
        tableData: [
          {
            city: '',
            area: '',
            address: '',
            total: '',  // 
            claimed: '',  // 
            completed: '', // 
            pk: 0 // ID
          }
        ],
        provinceList: []  // 
      };
    },
    methods: {
      seleProvince (val) {
        // 
        this.provinceSele = val;
        this.ajaxPersonTasks();
      },
      getCash (scope) {
        // 
        let taskInfo = {
          id: scope.row.pk,
          completed: scope.row.completed
        };
        this.$router.push({name: 'points', params: taskInfo});  // 
      },
      ajaxGetProList () {
        // 
        Ajax.get({api: 'get_task_province'}).then(res => {
          this.provinceList = res.body.province_list;
        });
      },
      ajaxPersonTasks () {
        // 
        let data = {province: this.provinceSele};
        Ajax.save({api: 'show_personal_task'}, data).then(res => {
          let data = res.body.personal_task;
          this.tableData = [{}];  // 
          data.forEach((val, index) => {
            let addressList = val.address.split('-');
            if (!this.tableData[index]) this.tableData.push({});
            [this.tableData[index].city,
              this.tableData[index].area,
              this.tableData[index].address] = [
              addressList[1],
              addressList[2],
              addressList[3]];
            this.tableData[index].total = val.total_task_num;
            this.tableData[index].claimed = val.get_task_num;
            this.tableData[index].completed = val.solve_task_num;
            this.tableData[index].pk = val.task_id;
          });
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import '../../../assets/css/common.less';

  .tableComplete {

  }

  .content {
    width: 1200px;
    margin: 0 auto 300px;
  }

  .table-box {
    .title-line {
      display: flex;
      justify-content: space-between;
      margin: 20px 0;
      h3, span {
        display: inline-block;
        font-size: 24px;
        color: @cCyan;
      }
      label {
        font-size: 12px;
        color: #000;
      }
    }
    .el-table {
      text-align: center;
    }
  }
</style>



