<template>
  <div class="upload">
    <div class="content">
      <div class="title-line">
        <h3>/</h3>
      </div>
      <div class="info-box">
        <div class="left">
          <h3></h3>
          <ul>
            <li>1</li>
            <li>2</li>
            <li> 31M jpgpngpdf</li>
            <li> 4</li>
          </ul>
        </div>
        <div class="right">
          <div class="btn" @click="$router.push('/mission/errs')">
            /
          </div>
          <div class="btn" @click="$router.push('/mission/upload')">
            
          </div>
          <div class="btn" @click="$router.push('/mission/search')">
            
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {};
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import '../../../assets/css/common.less';

  .content {
    width: 1200px;
    margin: 0 auto 300px;
  }

  .title-line {
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
    h3 {
      display: inline-block;
      font-size: 24px;
      color: @cCyan;
    }
  }

  .info-box {
    display: flex;
    justify-content: space-between;
    .left {
      width: 746px;
      height: 184px;
      border-top: 1px solid @cBorder;
      font-size: 24px;
      h3 {
        margin: 10px 0;
        color: @cCyan;
      }
    }
    .right {
      width: 315px;
      .btn {
        width: 100%;
        height: 59px;
        margin-bottom: 20px;
        background-color: @cCyan;
        cursor: pointer;
        text-align: center;
        line-height: 59px;
        font-size: 24px;
        color: @cWhite;
      }
      .btn:nth-child(1) {
        background-color: @cBorder;
        color: #000;
      }
    }
  }

</style>



