<template>
  <div class="brief">
    <div class="content">
      <a-map></a-map>
      <h3> </h3>
      <div class="detailTxt">
        <p>APP
        APP
        
        </p>
        <p>
        
          </p>
        2017421<br/>
        201753<br/>
        2017519APPbeta1.0<br/>
        2017691.0IOS
      </div>

      <img class="img" src="">
      <div class="more" @click="$router.push('/briefMore')"> >></div>

      <div class="moreImg">
        <ul class="ul">
          <li v-for="(item, index) in imgList">
            <img class="itemImg" src="item.img">
          </li>
        </ul>
        <img class="rightImg" src="">
      </div>

    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import aMap from '../../components/brief/aMap.vue';
  export default {
    components: {aMap},
    data () {
      return {
        imgList: [
          {img: ''},
          {img: ''},
          {img: ''},
          {img: ''},
          {img: ''},
          {img: ''}
        ]
      };
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">

  .content{
    width: 1200px;
    margin: 0 auto;
    margin-bottom: 100px;
    h3{
      margin: 15px;
      text-align: center;
    }
    .detailTxt{
      font-size: 20px;
      text-indent: 2em;
      line-height: 35px;
      p{
        margin-bottom: 20px;
      }
    }
    .img{
      width: 150px;
      height: 50px;
      margin-top: 180px;
    }
    .more{
      text-align: right;
      font-size: 18px;
      margin-top: 10px;
      margin-bottom: 20px;
      cursor: pointer;
    }
    .moreImg{
      width: 100%;
      display: flex;
      .ul{
        width: 1000px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-right: 40px;
      }
      .itemImg{
        width: 215px;
        height: 215px;
        display: flex;
        margin-bottom: 25px;
      }
      .rightImg{
        width: 455px;
        height: 455px;
      }
    }

  }




</style>



