<template>
  <div class="points">
    <small-title txt="" color="#59C1D2"></small-title>
    <form>
      <inp-line placeTxt="" v-model="formData.points"
                :disabled="true"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt=""
                :disabled="true"
                v-model="formData.bankAccount"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt=""
                :disabled="true"
                v-model="formData.bankName"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt="" inpType="password"
                v-model="formData.pwd" v-validate="'required'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt="" v-model="formData.phone"
                v-validate="'required|mobile'" name="">
        <span slot="left" class="left"></span>
      </inp-line>
      <inp-line placeTxt="" v-model="formData.imgCode"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
        <img slot="right" class="right imgCode" :src="imgCodeSrc" alt="...">
      </inp-line>
      <inp-line placeTxt="" v-model="formData.msgCode"
                v-validate="'required'" name="">
        <span slot="left" class="left"></span>
        <div slot="right" class="right" @click="getMsgCode"></div>
      </inp-line>
      <div class="submit" @click="getCash"></div>
      <div class="back" @click="$router.go(-1)">&lt;</div>
    </form>
  </div>
</template>

<script type="text/ecmascript-6">
  import Ajax from '@/api';
  import smallTitle from '../../components/common/smallTitle.vue';
  import inpLine from '../../components/common/inpLine.vue';

  export default {
    components: {smallTitle, inpLine},
    mounted () {
      this.initParams();  // 
      this.initBankInfo();  // 
    },
    data () {
      return {
        formData: {
          points: 0,  // 
          id: null, // ID
          bankAccount: null,  // 
          bankName: null, // 
          pwd: null,  // 
          phone: null,  // 
          imgCode: null,  // 
          msgCode: null // 
        }
      };
    },
    computed: {
      imgCodeSrc () {
        if (this.formData.phone && !this.errors.has('')) {
          return 'http://api.haozhaoli510.com/authCode?mobile=' + this.formData.phone;
        } else {
          return;
        }
      }
    },
    methods: {
      initParams () {
        let params = this.$route.params;
        this.formData.points = params.completed;
        this.formData.id = params.id;
      },
      initBankInfo () {
        Ajax.get({api: 'intergral_withdraw'}).then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.formData.bankAccount = data.bank_account;
            this.formData.bankName = data.bank_name;
          }
        });
      },
      getMsgCode () {
        // 
        let data = {
          mobile: this.formData.phone,
          imageCode: this.formData.imgCode,
          type: '3' // 1  2  3 
        };
        Ajax.save({api: 'send_msg'}, data).then(res => {
          if (res.body.status === 200) {
            this.$notify({title: '', type: 'success'});
          }
        });
      },
      getCash () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            this.ajaxGetCash();  // ajax
          } else {
            this.$alert(this.errors.all()[0], '');  // 
          }
        });
      },
      ajaxGetCash () {
        let data = {
          bank_account: this.formData.bankAccount,
          bank_name: this.formData.bankName,
          pay_pwd: this.formData.pwd,
          mobile: this.formData.phone,
          check_msg: this.formData.msgCode,
          task_id: this.formData.id
        };
        Ajax.save({api: 'intergral_withdraw'}, data).then(res => {
          if (res.body.status === 200) {
            this.$notify({message: '', type: 'success'});
            this.$router.push('/home');
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../assets/css/common.less";

  .points {
    width: 585px;
    margin: 40px auto 0;
  }

  .smallTitle {
    margin-bottom: 40px;
  }

  form {
    width: 100%;
    .inp-line {
      .left {
        box-sizing: border-box;
        width: 155px;
        padding: 0 20px;
        text-align-last: justify;
      }
      .right {
        padding: 0 10px;
        border-left: 1px solid #959595;
        cursor: pointer;
      }
      .imgCode {
        width: 5em;
        padding: 0;
        text-align: center;
      }
    }
    .submit {
      width: 100%;
      height: 64px;

      text-align: center;
      background-color: @cCyan;
      cursor: pointer;
      line-height: 64px;
      font-size: 24px;
      color: @cWhite;
    }
    .back {
      margin: 10px 0 100px;
      text-align: center;
      font-size: 24px;
      color: @cCyan;
    }
  }
</style>



